import axios from "axios";
import { createStore } from "vuex";

export default createStore({
  state: {
    i: 0,
    activeTab: "PHeader",
    activeTabTitle: "",
    currentLang: "en",
    ar: {
      header: {
        welcome: "",
        me: "",
        gallery: "",
        skills: "",
        services: "",
      },
      meSection: "",
      skillsSection: {
        general: ["", "", "", "", "", "", ""],
        frontend: ["", "", "", "", "", "", ""],
        backend: ["", "", ""],
      },
      servicesSection: ["", "", "", "", ["", ""]],
      gallerySection: ["", "", "", "", ""],
      classes: {
        title: "",
        paragraph: "",
        form: {
          name: "",
          gender: ["", ""],
          place: "",
          study: "",
          job: "",
          purpose: "",
        },
      },
    },
    en: {
      header: {
        welcome: "",
        me: "",
        gallery: "",
        skills: "",
        services: "",
      },
      meSection: "",
      skillsSection: {
        general: ["", "", "", "", "", "", ""],
        frontend: ["", "", "", "", "", "", ""],
        backend: ["", "", ""],
      },
      servicesSection: ["", "", "", "", ["", ""]],
      gallerySection: ["", "", "", "", ""],
      classes: {
        title: "",
        paragraph: "",
        form: {
          name: "",
          gender: ["", ""],
          place: "",
          study: "",
          job: "",
          purpose: "",
        },
      },
    },
  },
  mutations: {
    goBack() {
      this.state.activeTab = "PHeader";
    },
    changeTab(state, newTab) {
      state.activeTab = newTab;
    },
    changeTabTitle(state, newTabTitle) {
      state.activeTabTitle = newTabTitle;
    },
    changeLang(state, newLang) {
      state.currentLang = newLang;
    },
  },
  actions: {
    loadLangs(context) {
      axios.get("./languages/ar.json").then((result) => {
        context.state.ar = result.data;
      });
      axios.get("./languages/en.json").then((result) => {
        context.state.en = result.data;
      });
    },
  },
  modules: {},
});
