<template>
  <div class="back-button">
    <button id="back" @click="goBack">
      <i :class="'fas fa-arrow-' + direction"></i>
    </button>
    <b>
      {{ activeTabTitle }}
    </b>
  </div>
</template>

<script>
export default {
  name: "PBack",
  computed: {
    activeTab() {
      return this.$store.state.activeTab;
    },
    activeTabTitle() {
      return this.$store.state.activeTabTitle;
    },
    direction() {
      if (document.querySelector("html").lang === "en") {
        return "left";
      } else {
        return "right";
      }
    },
  },
  methods: {
    goBack() {
      this.$store.commit("goBack");
    },
  },
};
</script>

