<template>
  <section class="header-section">
    <header>
      <div class="languages">
        <button @click="changeLang('ar')">Ar</button> |
        <button @click="changeLang('en')">En</button>
      </div>
      <div class="image-container">
        <img src="../../public/logo.svg" alt="logo" />
      </div>
      <h2 v-html="currentLang.header.welcome"></h2>
      <nav>
        <ul>
          <li @click="changeTab('me', $event)">{{ currentLang.header.me }}</li>
          <li id="gallery-toggle" @click="changeTab('gallery', $event)">
            {{ currentLang.header.gallery }}
          </li>
          <li @click="changeTab('skills', $event)">
            {{ currentLang.header.skills }}
          </li>
          <li @click="changeTab('services', $event)">
            {{ currentLang.header.services }}
