</template>

<script>
export default {
  name: "PHeader",
  computed: {
    currentLang() {
      return this.$store.state[this.$store.state.currentLang];
    },
  },

  methods: {
    changeTab(newTab, e) {
      this.$store.commit("changeTab", newTab);
      this.$store.commit("changeTabTitle", e.currentTarget.textContent);
    },
    changeLang(newLang) {
      document.querySelector("html").lang = newLang;
      if (newLang === "en") {
        document.querySelector("html").dir = "ltr";
      } else if (newLang === "ar") {
        document.querySelector("html").dir = "rtl";
      }
      this.$store.commit("changeLang", newLang);
      this.$store.commit("goBack");
    },
  },
};
</script>

