<template>
  <div id="page-container" class="bg">
    <router-view v-show="!loadingPage" />
    <m-logo v-if="loadingPage" />
  </div>
</template>

<script>
import MLogo from "./components/MLogo.vue";
export default {
  name: "mainScreen",
  data() {
    return {
      loadingPage: true,
    };
  },
  components: { MLogo },
  computed: {
    activeTab() {
      return this.$store.state.activeTab;
    },
    activeTabTitle() {
      return this.$store.state.activeTabTitle;
    },
  },
  beforeMount() {
    this.$store.dispatch("loadLangs");
  },
  mounted() {
    setTimeout(() => {
      this.loadingPage = false;
    }, 10200);
  },
};
</script>

