<template>
  <section>
    <p-back />
    <div class="content">
      <ul class="menu">
        <li>
          <i class="fas fa-code fa-3x"></i>
          <b>{{ currentLang.servicesSection[0] }}</b>
        </li>
        <li>
          <i class="fas fa-paint-brush fa-3x"></i>
          <b>{{ currentLang.servicesSection[1] }}</b>
        </li>
        <li>
          <i class="fas fa-desktop fa-3x"></i>
          <b>{{ currentLang.servicesSection[2] }}</b>
        </li>
        <li>
          <i class="fas fa-edit fa-3x"></i>
          <b>{{ currentLang.servicesSection[3] }}</b>
        </li>
        <li class="classes">
          <i class="fas fa-globe-americas fa-4x"></i>
          <div>
            <h4>{{ currentLang.servicesSection[4][0] }}</h4>
            <p>{{ currentLang.servicesSection[4][1] }}</p>
          </div>
          <router-link to="/classes">
            <button>
              <i :class="'fas fa-arrow-' + direction"></i>
            </button>
          </router-link>
        </li>
      </ul>
    </div>
  </section>
</template>

<script>
import PBack from "../PBack.vue";
export default {
  name: "services",
  components: { PBack },
  methods: {
    open(e) {
      e.currentTarget.classList.add("open");
    },
    close(e) {
      e.currentTarget.parentElement.classList.remove("open");
    },
  },
  computed: {
    direction() {
      if (document.querySelector("html").lang === "en") {
        return "right";
      } else {
        return "left";
      }
    },
    currentLang() {
      return this.$store.state[this.$store.state.currentLang];
    },
  },
};
</script>

